.note-summaries {
  margin: $spacing--xlarge 0; }

.note-summary {
  @include tag-color(border-color);

  display: block;

  // NOTE: I'm using padding instead of margins so there are no gaps in the hovers when moving the
  // mouse between links.
  margin-top: $spacing;
  margin-bottom: $spacing;
  padding-left: calc(#{ $spacing * 0.75 } - 3px);

  border-left-width: 3px;
  border-left-style: solid;

  text-decoration: none;

  transition: background-color $transition-time--small ease-in, box-shadow $transition-time--small ease-in;

  // Compensate for the padding.
  &:first-of-type {
    margin-top: -$spacing--small; }

  &:last-of-type {
    margin-bottom: -$spacing--small; }

  &:hover {
    background-color: darken($alabaster, 1%);
    box-shadow: 0 0 0 $spacing--small darken($alabaster, 1%); }

  &--hidden {
    display: none; }

  &__title, &__about {
    margin: 0; }

  @include viewport--max-medium {

    &__authors, &__about {
      display: block; }

    &__source {
      font-style: italic; }

    &__separator {
      display: none; } } }
