.landing {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  &__svg {
    display: block;

    width: calc(100% - 6vw);
    height: calc(100% - 6vw);

    @include portrait {
      width: calc(100% - 8vh);
      height: calc(100% - 8vh); } }

  &__item {
    fill: url(#flannel); }

  &__link {
    pointer-events: bounding-box;

    // BUG FIX: This is not the default in Safari.
    cursor: pointer;

    &:hover .landing__item {
      fill: $cornflower-blue; } }

  &__comma {
    @include portrait {
      display: none; } } }
