.header {
  text-align: center;

  // NOTE: Use $spacing--large for the bottom because the first note has
  // padding on the top.
  margin: $spacing--large 0;

  &__header {
    margin: 0; }

  &__suphead, &__subhead {
    margin: $spacing--xsmall 0;
    font-style: italic;

    // Slightly darken the text to compensate for the smaller font size
    color: darken($emperor, 5%); }

  // HACK: The extra spacing is needed to visually make the header look evenly spaced.
  &__subhead {
    margin: $spacing--small 0; }

  &__title-link {
    text-decoration: none;

    &:not(:hover) {
      color: inherit; } }

  &__tags {
    display: flex;
    justify-content: center;
    flex-wrap: wrap; }

  // HACK: The tag groups exist to allow the tags to wrap in a grouped fasion. This prevents one
  // "lone tag" from sitting on the next line on standard mobile sizes.
  &__tag-group {
    display: flex;

    * {
      margin: $spacing--small $spacing--xsmall 0; } } }
