@mixin portrait {
  @media screen and (max-aspect-ratio: 1/1) {
    @content; } }

@mixin viewport--medium {
  @media only screen and (min-width: $viewport-width--medium) {
    @content; } }

@mixin viewport--large {
  @media only screen and (min-width: $viewport-width--large) {
    @content; } }

@mixin viewport--max-medium {
  @media only screen and (max-width: #{ $viewport-width--medium - 1px }) {
    @content; } }

@mixin viewport--max-large {
  @media only screen and (max-width: #{ $viewport-width--large - 1px }) {
    @content; } }

@mixin print {
  @media print {
    @content; } }
