@charset "UTF-8";
@import url(normalize.css);
.hljs-comment,
.hljs-quote {
  color: #8f8f8f;
  font-style: italic; }

.hljs-variable,
.hljs-template-variable,
.hljs-tag,
.hljs-name,
.hljs-selector-id,
.hljs-selector-class,
.hljs-regexp,
.hljs-deletion {
  color: #ff6d71; }

.hljs-number,
.hljs-built_in,
.hljs-builtin-name,
.hljs-literal,
.hljs-type,
.hljs-params,
.hljs-meta,
.hljs-link,
.hljs-attribute {
  color: #955fc2; }

.hljs-string,
.hljs-symbol,
.hljs-bullet,
.hljs-addition {
  color: #ca6399; }

.hljs-title,
.hljs-section {
  color: #627ff6; }

.hljs-keyword,
.hljs-selector-tag {
  color: #836fdd; }

.hljs-emphasis {
  font-style: italic; }

.hljs-strong {
  font-weight: bold; }

a {
  color: inherit;
  color: var(--theme-color);
  transition: color 0.075s ease-in; }

audio {
  display: block;
  width: 100%;
  margin: 1.875rem 0; }

b {
  font-weight: inherit; }

blockquote {
  margin: 1.25rem 0;
  border-left: 3px solid var(--theme-color);
  padding-left: calc(0.9375rem - 3px); }
  blockquote * {
    display: inline; }
  blockquote p {
    display: block; }

body {
  height: 100%;
  display: flex;
  flex-direction: column; }
  body * {
    flex: 0 0 auto; }

cite {
  display: block;
  font-style: italic; }
  cite::before {
    content: "—"; }

code {
  font-family: "Source Code Pro", monospace;
  font-size: 0.8rem; }

html {
  height: 100%;
  font-family: "Gentium Book Basic", serif;
  font-size: 19px;
  font-weight: 400;
  line-height: 1.45;
  color: #555; }
  @media only screen and (min-width: 420px) {
    html {
      font-size: 21px; } }
  @media only screen and (min-width: 960px) {
    html {
      font-size: 22px; } }

.icon {
  display: inline-block;
  fill: currentColor;
  vertical-align: middle;
  width: 1.125rem;
  height: 1.125rem; }

iframe {
  overflow: hidden; }

ol, ul {
  padding-left: 1.25rem; }

ol {
  counter-reset: ordered-list; }

li {
  margin: 0.625rem 0;
  list-style: none;
  position: relative; }
  li::before {
    color: var(--theme-color);
    position: absolute;
    left: -1.25rem; }

ul li::before {
  content: "•"; }

ol li::before {
  content: counter(ordered-list) ". ";
  counter-increment: ordered-list; }

p {
  margin: 1.25rem 0; }

pre {
  margin: 1.25rem 0;
  background-color: #f7f7f7;
  border-radius: 0.3125rem;
  overflow-x: auto;
  max-width: calc(100vw - 1.25rem); }
  pre code {
    display: block;
    padding: 0.3125rem 0.625rem; }

q, blockquote p {
  quotes: "“" "”" "‘" "’";
  font-style: italic; }
  q::before, blockquote p::before {
    content: open-quote; }
  q::after, blockquote p::after {
    content: close-quote; }

section {
  margin: 0;
  padding: 0; }

h1, h2, h3 {
  color: #333;
  font-weight: 700;
  margin: 1.25rem 0;
  font-family: "Open Sans", sans-serif; }

h1 {
  font-size: 1.2rem; }
  @media only screen and (min-width: 420px) {
    h1 {
      font-size: 1.5rem;
      line-height: 2rem; } }

h2 {
  font-size: 1rem; }
  @media only screen and (min-width: 420px) {
    h2 {
      font-size: 1.2rem; } }

h3 {
  font-size: 0.9rem;
  line-height: inherit; }

p {
  margin: 1.25rem 0; }

*, *::before, *::after {
  box-sizing: border-box; }

.content {
  max-width: 60ch;
  margin: 0 auto;
  padding: 0 0.625rem; }
  @media only screen and (min-width: 420px) {
    .content {
      padding: 0 1.25rem; } }

.disclaimer {
  position: relative;
  margin: 1.875rem 0;
  padding: 0.625rem;
  font-style: italic;
  background-color: #f7f7f7; }

.header {
  text-align: center;
  margin: 1.875rem 0; }
  .header__header {
    margin: 0; }
  .header__suphead, .header__subhead {
    margin: 0.3125rem 0;
    font-style: italic;
    color: #484848; }
  .header__subhead {
    margin: 0.625rem 0; }
  .header__title-link {
    text-decoration: none; }
    .header__title-link:not(:hover) {
      color: inherit; }
  .header__tags {
    display: flex;
    justify-content: center;
    flex-wrap: wrap; }
  .header__tag-group {
    display: flex; }
    .header__tag-group * {
      margin: 0.625rem 0.3125rem 0; }

.landing {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%; }
  .landing__svg {
    display: block;
    width: calc(100% - 6vw);
    height: calc(100% - 6vw); }
    @media screen and (max-aspect-ratio: 1 / 1) {
      .landing__svg {
        width: calc(100% - 8vh);
        height: calc(100% - 8vh); } }
  .landing__item {
    fill: url(#flannel); }
  .landing__link {
    pointer-events: bounding-box;
    cursor: pointer; }
    .landing__link:hover .landing__item {
      fill: #627ff6; }
  @media screen and (max-aspect-ratio: 1 / 1) {
    .landing__comma {
      display: none; } }

.main-navigation {
  margin: 0.9375rem 0;
  font-size: 0.8rem; }
  @media only screen and (min-width: 960px) {
    .main-navigation {
      display: flex;
      justify-content: center;
      padding: 0 0.625rem; } }
  .main-navigation__primary, .main-navigation__secondary {
    display: flex;
    justify-content: center; }
  @media only screen and (min-width: 960px) {
    .main-navigation__secondary {
      margin-left: auto; } }
  @media only screen and (max-width: 959px) {
    .main-navigation__secondary {
      margin-top: 0.9375rem; } }
  @media only screen and (max-width: 419px) {
    .main-navigation__title-text--long {
      display: none; } }
  @media only screen and (min-width: 420px) {
    .main-navigation__title-text--short {
      display: none; } }
  .main-navigation__link, .main-navigation__icon {
    display: block;
    margin: 0 0.625rem; }
  .main-navigation__link {
    font-family: "Open Sans", sans-serif;
    text-decoration: none;
    color: #8f8f8f;
    transition: color 0.075s ease, box-shadow 0.075s ease-in; }
    .main-navigation__link:hover {
      color: inherit; }
    .main-navigation__link:hover {
      box-shadow: 0 3px #627ff6; }
    .main-navigation__link--selected {
      box-shadow: 0 2px #b5b5b5; }
    .main-navigation__link:nth-child(n + 5) {
      order: 1; }
  .main-navigation__icon {
    color: #aeaeae; }
    .main-navigation__icon:hover {
      color: #627ff6; }
    .main-navigation__icon svg {
      transition: fill 0.075s ease; }

.note-footer {
  margin: 1.875rem 0;
  font-size: 0.8rem;
  color: #999; }

.note-summaries {
  margin: 2.5rem 0; }

.note-summary {
  display: block;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  padding-left: calc(0.9375rem - 3px);
  border-left-width: 3px;
  border-left-style: solid;
  text-decoration: none;
  transition: background-color 0.1s ease-in, box-shadow 0.1s ease-in; }
  .note-summary[data-type="article"] {
    border-color: #627ff6; }
  .note-summary[data-type="book"] {
    border-color: #836fdd; }
  .note-summary[data-type="video"] {
    border-color: #955fc2; }
  .note-summary[data-type="talk"] {
    border-color: #ca6399; }
  .note-summary[data-type="podcast"] {
    border-color: #ff6d71; }
  .note-summary:first-of-type {
    margin-top: -0.625rem; }
  .note-summary:last-of-type {
    margin-bottom: -0.625rem; }
  .note-summary:hover {
    background-color: #f4f4f4;
    box-shadow: 0 0 0 0.625rem #f4f4f4; }
  .note-summary--hidden {
    display: none; }
  .note-summary__title, .note-summary__about {
    margin: 0; }
  @media only screen and (max-width: 419px) {
    .note-summary__authors, .note-summary__about {
      display: block; }
    .note-summary__source {
      font-style: italic; }
    .note-summary__separator {
      display: none; } }

.note {
  margin: 1.875rem 0; }
  .note[data-type="article"] {
    --theme-color: #627ff6; }
  .note[data-type="book"] {
    --theme-color: #836fdd; }
  .note[data-type="video"] {
    --theme-color: #955fc2; }
  .note[data-type="talk"] {
    --theme-color: #ca6399; }
  .note[data-type="podcast"] {
    --theme-color: #ff6d71; }

.tag {
  display: inline-block;
  padding: calc(0.15625rem - 1px) calc(0.625rem - 1px);
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 0.7rem;
  font-weight: normal;
  text-transform: uppercase;
  border-radius: 1.25rem;
  letter-spacing: 0.05em;
  text-decoration: none;
  border-width: 1px;
  border-style: solid;
  transition: box-shadow 0.075s ease-in; }
  .tag[data-type="article"] {
    background-color: #627ff6; }
  .tag[data-type="book"] {
    background-color: #836fdd; }
  .tag[data-type="video"] {
    background-color: #955fc2; }
  .tag[data-type="talk"] {
    background-color: #ca6399; }
  .tag[data-type="podcast"] {
    background-color: #ff6d71; }
  .tag[data-type="article"] {
    border-color: #627ff6; }
  .tag[data-type="book"] {
    border-color: #836fdd; }
  .tag[data-type="video"] {
    border-color: #955fc2; }
  .tag[data-type="talk"] {
    border-color: #ca6399; }
  .tag[data-type="podcast"] {
    border-color: #ff6d71; }
  .tag[data-type="article"]:hover {
    box-shadow: 0 0 0 0.11719rem #92a6f9; }
  .tag[data-type="book"]:hover {
    box-shadow: 0 0 0 0.11719rem #a798e7; }
  .tag[data-type="video"]:hover {
    box-shadow: 0 0 0 0.11719rem #ad84d0; }
  .tag[data-type="talk"]:hover {
    box-shadow: 0 0 0 0.11719rem #d789b2; }
  .tag[data-type="podcast"]:hover {
    box-shadow: 0 0 0 0.11719rem #ffa0a3; }
  @media only screen and (min-width: 420px) {
    .tag {
      font-size: 0.6rem; } }
  .tag.tag.tag--selected[data-type="article"] {
    box-shadow: 0 0 0 0.23438rem #92a6f9; }
  .tag.tag.tag--selected[data-type="book"] {
    box-shadow: 0 0 0 0.23438rem #a798e7; }
  .tag.tag.tag--selected[data-type="video"] {
    box-shadow: 0 0 0 0.23438rem #ad84d0; }
  .tag.tag.tag--selected[data-type="talk"] {
    box-shadow: 0 0 0 0.23438rem #d789b2; }
  .tag.tag.tag--selected[data-type="podcast"] {
    box-shadow: 0 0 0 0.23438rem #ffa0a3; }
